<template>
    <div class="container">
        <div class="box">
            <div class="top">
                <div class="img">
                    <label>Hi ^^</label>
                    <hr>
                    <p>ACOUSTIC</p>
                    <hr>
                    <label>Tuấn Em</label>
                </div>
                <div class="spin" :class="{spinshow: play}" :style="{backgroundImage: `url(${require('./assets/lon.jpg')})`}"></div>
            </div>
            <div class="icon">
                <i class="fas fa-long-arrow-alt-left" @click="() => this.$router.push('/')"></i>
                <img src="./assets/play.png" class="play" @click="Play()" v-if="!play">
                <img src="./assets/pause.png" class="pause" @click="Pause()" v-if="play">
                <img src="./assets/undo.png" class="undo" @click="Undo()">
            </div>
        </div>
        <audio ref="music">
            <source src="./assets/music.mp3">
        </audio>
    </div>
</template>

<script>

export default {
    name: 'Music',
    data() {
        return{
            play: false
        }
    },
    methods: {
        Play(){
            var music = this.$refs.music
            music.play()
            this.play = true
        },
        Pause(){
            var music = this.$refs.music
            music.pause()
            this.play = false
        },
        Undo(){
            var music = this.$refs.music
            music.currentTime = 0
        }
    }
}
</script>

<style scoped>

.icon img {
    cursor: pointer;
    width: 1.5em;
}

.box {
    width: 12em;
    height: auto;
    padding: 1em;
    background-color: #053c60;
    border-radius: 1em;
    box-shadow: 0em 0em 0.5em 0em #005994;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.container {
    width: 100%;
    min-height: 100vh;
    background-color: #d1fcff;
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.top {
    width: 11.6em;
    height: 11.6em;
    margin-bottom: 1em;
    position: relative;
    border: 0.2em solid white;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.top p {
    color: blue;
    margin: 0.3em 0em;
}

.top hr {
    width: 150%;
    margin-left: -25%;
    background-color: blue;
    border: 0.05em solid blue;
}

.img {
    z-index: 1;
    text-align: center;
    color: rgb(5, 5, 94);
}

.spin {
    width: 11.6em;
    height: 11.6em;
    border-top: 0.2em solid rgba(255, 255, 255, 0);
    border-bottom: 0.2em solid rgba(255, 255, 255, 0);
    border-left: 0.2em solid blue;
    border-right: 0.2em solid blue;
    position: absolute;
    top: -0.2em;
    left: -0.2em;
    background-repeat: no-repeat;
    background-size: contain;
    border-radius: 50%;
}

.spinshow {
    animation-name: spin;
    animation-duration: 5000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    } 
    to {
        transform: rotate(360deg);
    }
}

i.fas.fa-long-arrow-alt-left {
    margin-right: 0.3em;
    padding: 0.1em;
    cursor: pointer;
    font-size: 1.3em;
    color: #00c4ff;
}

.icon {
    display: flex;
    flex-direction: row;
    align-items: center;
}

</style>
